.logo {
  display: flex;
  align-items: center;

  font-family: @charter;
  font-weight: 400;
}

.logo__img-wrap {
  width: 82px;
  height: 70px;
  &--big {
    width: 144px;
    height: 122px;
    margin-bottom: 12px;
  }
}

.logo__img {
  .img-contain;
  &--position {
    object-position: -5px;
  }
}

.logo__text-wrap {
  width: 185px;
}

.logo__text {
  font-size: 16px;
  line-height: 20px;

  text-align: center;
}

.logo__text-item {
  display: block;

  font-weight: 400;
  line-height: 22px;
  text-transform: uppercase;
  text-align: left;
}

.footer__copyright-logo {
  width: 20px;
  height: 20px;
  fill: #808080;
}